<template>
    <div id="app" class="view">
        <div class="password-box">
            <input :type="input_type" placeholder="Enter your password"
                v-model="password">
            <span class="fas fa-eye" @click="isPswVisible = !isPswVisible"></span>
        </div>
        <div class="level-container">
            <div :class="strength_level"></div>
        </div>
        <ul>
            <li v-for="(rule, index) in rules" :key="index"
                :class="rule_status[index]">
                <span v-if="rule_status[index] === ''" class="fas fa-times"></span>
                <span v-else class="fas fa-check"></span>
                {{ rule.label }}
            </li>
        </ul>
    </div>
</template>

<script>
import crypto from "crypto";
import https from "https";

export default {
    name: 'App',
    data: function() {
        return {
            isPswVisible: false,
            rules: [
                {label: "Password length 8 or more characters", passed: false, check: this.checkPswLength},
                {label: "At least one lowercase character found", passed: false, check: this.checkPswLowercase},
                {label: "At least one uppercase character found", passed: false, check: this.checkPswUppercase},
                {label: "At least one number found", passed: false, check: this.checkPswDigit},
                {label: "At least one special character found", passed: false, check: this.checkPswSpecialChars},
                {label: "Password not leaked", passed: true, check: this.checkPswLeak},
            ],
            password: "",
            timerId: null,
            hash: null,
            strength: 0
        };
    },
    methods: {
        checkPswLength: function() {
            return this.password.length >= 8;
        },
        checkPswLowercase: function() {
            return /[a-z]+/.test(this.password);
        },
        checkPswUppercase: function() {
            return /[A-Z]+/.test(this.password);
        },
        checkPswDigit: function() {
            return /\d+/.test(this.password);
        },
        checkPswSpecialChars: function() {
            return /[^a-zA-Z0-9]+/.test(this.password);
        },
        checkPswLeak: function() {
            if(this.timerId) {
                clearTimeout(this.timerId);
            }
            
            this.timerId = setTimeout(() => this.haveIBeenPwned(), 1e3);
        },
        
        haveIBeenPwned: function() {
            // Reset the instance of the Hash class
            this.hash = crypto.createHash("sha1");
            
            // Compute the hash
            this.hash.update(this.password);
            let hexed_psw = this.hash.digest("hex"),
                rule = this.rules[this.rules.length -1];
            
            https.get(`https://api.pwnedpasswords.com/range/${hexed_psw.substr(0, 5)}`, res => {
                res.on("data", data => {
                    data = data.toString();
                    let index = data.indexOf(hexed_psw.substr(5).toUpperCase());
                    
                    if(index === -1) {
                        rule.passed = true;
                        rule.label = "Password not leaked";
                    }
                    else {
                        rule.passed = false;
                        
                        // Find the line of the remaining part of the hash and grab the number of times it exists
                        let line_end_index = data.indexOf("\n", index),
                            times = data.substring(index, line_end_index).split(":")[1];
                        rule.label = `Password found ${times} times`;
                    }
                });
            }).on("error", err => console.error(err));
        }
    },
    computed: {
        input_type: function() {
            return this.isPswVisible ? "text" : "password";
        },
        rule_status: function() {
            return this.rules.map(value => value.passed ? "passed" : "");
        },
        strength_level: function() {
            return `level level-${this.strength}`;
        }
    },
    watch: {
        password: function() {
            this.rules.forEach(value => {
                value.passed = value.check();
            });
        },
        rule_status: function(array) {
            this.strength = array.filter(value => value === "passed").length;
        }
    }
}
</script>
